/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}


.slick-arrow {
    width: 60px;
    height: 60px;
    background: #FFFFFF;
    border: none;
    @media all and (max-width: 770px) {
        width: 40px;
    height: 40px;
        }
    
}
.slick-arrow:hover {
    background: #53CD8B;
    border: none;
    
}


button > .slick-prev, 
button > .slick-next {
  position: absolute;
}


.slick-prev {
    position: absolute;
    top: -88px;
    right: 88px;
    @media all and (max-width: 770px) {
        top: 456px;
        }
        @media all and (max-width: 450px) {
            right: 76px;
            }
            @media all and (max-width: 400px) {
                top: 438px; 
            }
}
.slick-next {
    position: absolute;
    top: -88px;
    right: 0px;
    @media all and (max-width: 770px) {
        top: 456px;
        }
        @media all and (max-width: 400px) {
            top: 438px; 
        }
}
.slick-slide {
    margin: 0 20px;
    @media all and (max-width: 400px) {
        margin: 0 5px; 
    }
    
}

.slider-track {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    }
